.wrapper {
  justify-content: center;
  gap: 40px;
  margin-top: 150px;
  display: flex;
}

button {
  width: 60px;
  height: 40px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .8);
  border: 1px solid #d7d7c3;
  border-radius: 4px;
  font-weight: 500;
  box-shadow: 0 -1px 10px rgba(0, 0, 0, .75);
}

button:disabled {
  cursor: auto;
}

p {
  font-size: 25px;
}

